import { PrismaClient } from '@prisma/client';

/* eslint-disable no-var */
declare global {
  var prisma: PrismaClient | undefined;
}
/* eslint-enable no-var */

// Configurar a URL do banco com parmetros de connection pool
const databaseUrl = process.env.DATABASE_URL; // no usar fallback para evitar conectar no DB errado

// Prevenir mltiplas instncias do Prisma Client em desenvolvimento
// e garantir uma nica instncia em produo
const prismaClientSingleton = () => {
  return new PrismaClient({
    log: ['error', 'warn'],
    datasources: {
      db: {
        url: databaseUrl,
      },
    },
  });
};

// Usar o objeto global para armazenar a instncia entre hot-reloads em desenvolvimento
// ou criar uma nova instncia em produo
export const prisma = globalThis.prisma ?? prismaClientSingleton();

// Sempre armazenar no objeto global, independente do ambiente
globalThis.prisma = prisma;